import React from "react";
import classes from "./Login.module.scss";
import { connect } from "react-redux";
import {
  mapStateToProps,
  mapDispatchToProps,
} from "../../../redux/reducers/reducerNav/reducerNav";

const LoginRender: React.FC<{
  isMenuBtnActive: boolean;
  setIsMenuBtnActive: Function;
}> = ({ isMenuBtnActive, setIsMenuBtnActive }) => {
  return (
    <div
      className={
        isMenuBtnActive
          ? `${classes["login"]} ${classes["login_active"]}`
          : classes["login"]
      }
    >
      <button
        className={classes["login__btn"]}
        onClick={() => setIsMenuBtnActive()}
      >
        <i className={"material-icons " + classes["login__img"]}>
          account_circle
        </i>
        <span className={classes["login__text"]}>Sign In</span>
      </button>
    </div>
  );
};
export const Login = connect(mapStateToProps, mapDispatchToProps)(LoginRender);
