import React, { useState, useEffect } from "react";
import classes from "./Parralax.module.scss";
import { NavLink } from "react-router-dom";

export const Parralax: React.FC<{
  bgColor: string;
  img: string;
  title: string;
  caption: string;
  btnName: string;
  link: string;
  startPoint: number;
  endPoint: number;
}> = ({
  img,
  title,
  caption,
  btnName,
  bgColor,
  link,
  startPoint,
  endPoint,
}) => {
  const [position, setPosition] = useState(-100);
  const [currentScrollPosition, setCurrentScrollPosition] = useState(0);

  useEffect(() => {
    const scrollHandler = (e: any) => {
      if (
        window.scrollY >= startPoint &&
        window.scrollY - currentScrollPosition >= 1 &&
        position < 200
      ) {
        setPosition(position + (window.scrollY - currentScrollPosition) / 5);
      }
      if (
        window.scrollY <= endPoint &&
        currentScrollPosition - window.scrollY >= 1 &&
        position > -100
      ) {
        setPosition(position - (currentScrollPosition - window.scrollY) / 5.5);
      }
      setCurrentScrollPosition(window.scrollY);
    };
    window.addEventListener("scroll", scrollHandler);
    return function cleanup() {
      window.removeEventListener("scroll", scrollHandler);
    };
  }, [position, currentScrollPosition, startPoint, endPoint]);

  return (
    <div
      className={classes["parallax"]}
      style={{
        backgroundPosition: `50% ${position}%`,
        backgroundColor: `${bgColor}`,
        backgroundImage: `url(${img})`,
      }}
    >
      <h3 className={classes["parallax__caption"]}>{caption}</h3>
      <h2 className={classes["parallax__title"]}>{title}</h2>
      <NavLink to={link} className={classes["parallax__btn"]}>
        {btnName}
      </NavLink>
    </div>
  );
};
