import React from "react";
import classes from "./ContainerText.module.scss";

export const ContainerText: React.FC<{
  title: string;
  caption: string;
  paragraph1: string;
  paragraph2?: string;
}> = ({ title, caption, paragraph1, paragraph2 }) => {
  return (
    <div className={classes["container-text"]}>
      <h2 className={classes["container-text__title"]}>{title}</h2>
      <h3 className={classes["container-text__caption"]}>{caption}</h3>
      <p className={classes["container-text__paragraph"]}>{paragraph1}</p>
      <p className={classes["container-text__paragraph"]}>{paragraph2}</p>
    </div>
  );
};
