import React from "react";
import classes from "./Footer.module.scss";
import { FooterSocial } from "./FooterSocial/FooterSocial";

export const Footer: React.FC = () => {
  return (
    <div className={classes["footer"]}>
      <div className={classes["footer__content"]}>
        <div className={classes["footer__info"]}>
          <div className={classes["footer__info-container"]}>
            <h2 className={classes["footer__info-title"]}>ADDRESS</h2>
            <p className={classes["footer__info-text"]}>
              500 Terry Francois St.
            </p>
            <p className={classes["footer__info-text"]}>
              San Francisco, CA 94158
            </p>
            <p className={classes["footer__info-text"]}>CA 94158</p>
          </div>
          <div className={classes["footer__info-container"]}>
            <h2 className={classes["footer__info-title"]}>CONTACT</h2>
            <p className={classes["footer__info-text"]}>info@mysite.com</p>
            <p className={classes["footer__info-text"]}>Tel: 123-456-7890</p>
          </div>
          <div className={classes["footer__info-container"]}>
            <h2 className={classes["footer__info-title"]}>HOURS</h2>
            <p className={classes["footer__info-text"]}>OPEN DAILY</p>
            <p className={classes["footer__info-text"]}>10AM-8PM</p>
          </div>
          <div className={classes["footer__info-container"]}>
            <h2 className={classes["footer__info-title"]}>MAILING LIST</h2>
            <form className={classes["footer__form"]}>
              <input
                type="email"
                name="email"
                placeholder=" Enter your email here*"
                className={classes["footer__form-input"]}
              />
              <button type="submit" className={classes["footer__form-btn"]}>
                SUBSCRIBE
              </button>
            </form>
          </div>
        </div>
        <div className={classes["footer__caption"]}>
          <div className={classes["footer__info-text"]}>
            2020 by Gelato. Proudly created by{" "}
            <a
              className={classes["footer__caption-link"]}
              target="_blank"
              rel="noreferrer"
              href="https://www.linkedin.com/in/maksym-bordonos-b7a47a131"
            >
              MaksBordo
            </a>
          </div>
          <FooterSocial />
        </div>
      </div>
    </div>
  );
};
