import React, { useState, useEffect } from "react";
import classes from "./Parralax2.module.scss";
import img from "../../../../../assets/images/home/p3.png";
import img2 from "../../../../../assets/images/home/p4.png";
export const Parralax2: React.FC = () => {
  const [position, setPosition] = useState(100);
  const [currentScrollPosition, setCurrentScrollPosition] = useState(0);

  useEffect(() => {
    const scrollHandler = (e: any) => {
      if (
        window.scrollY >= 3400 &&
        window.scrollY - currentScrollPosition >= 1 &&
        position > 0
      ) {
        setPosition(position - 2);
      }
      if (
        window.scrollY <= 4500 &&
        currentScrollPosition - window.scrollY >= 1 &&
        position < 100
      ) {
        setPosition(position + 2);
      }
      setCurrentScrollPosition(window.scrollY);
    };
    window.addEventListener("scroll", scrollHandler);
    return function cleanup() {
      window.removeEventListener("scroll", scrollHandler);
    };
  }, [position, currentScrollPosition]);

  return (
    <div
      className={classes["parallax2"]}
      style={{
        backgroundPosition: `50% ${position}%`,
        backgroundImage: `url(${img})`,
      }}
    >
      <img
        src={img2}
        alt="ice-cream"
        className={classes["parallax2__img"]}
      ></img>
    </div>
  );
};
