import React, { Fragment } from "react";
import classes from "./Home.module.scss";
import { connect } from "react-redux";
import { mapStateToProps } from "../../../redux/reducers/reducerHome/reducerHome";

import { Footer } from "../../Footer/Footer";
import { Container1 } from "./Containers/Container1/Container1";
import { Container2 } from "./Containers/Container2/Container2";
import { Parralax } from "./Containers/Parralax/Parralax";
import { Container3 } from "./Containers/Container3/Container3";
import { Parralax2 } from "./Containers/Parralax2/Parralax2";
import img3 from "../../../assets/images/home/3.jpg";
import p1 from "../../../assets/images/home/p1.png";
import p2 from "../../../assets/images/home/p2.png";
import { Carousel } from "./Containers/Carousel/Carousel";

const HomeRender: React.FC<{ img: any }> = ({ img }) => {
  return (
    <Fragment>
      <div className={classes["home"]}>
        <Container1 />
        <Container2
          img={img[0]}
          title="OUR FLAVORS"
          caption="Fresh n' Tasty!"
          position={100}
        />
        <Parralax
          bgColor="#b5e8d5"
          img={p1}
          title="DAIRY FREE"
          caption="ENJOY"
          btnName="MENU"
          link="/flavors"
          startPoint={600}
          endPoint={2200}
        />
        <Container3
          img={img3}
          title="OUR PLACE"
          caption="Ice cream by the sea"
          position={100}
        />
        <Parralax
          bgColor="#ffbfb3"
          img={p2}
          title="EVENTS"
          caption="CATERING & MORE"
          btnName="MAKE A WISH"
          link="/book"
          startPoint={2200}
          endPoint={3800}
        />
        <Container2
          img={img[1]}
          title="DESSERTS"
          caption="Ice cream goodies"
          position={50}
        />
        <Parralax2 />
        <h2 className={classes["home__hashtag"]}>#DOUBLE TAP</h2>
        <Carousel />
      </div>
      <Footer />
    </Fragment>
  );
};
export const Home: React.FC = connect(mapStateToProps)(HomeRender);
