import React, { Fragment } from "react";
import classes from "./Flavors.module.scss";

import { ContainerTop } from "../ContainerTop/ContainerTop";
import { Cart } from "../../Cart/Cart";
import { Footer } from "../../Footer/Footer";

export const Flavors: React.FC = () => {
  return (
    <Fragment>
      <div className={classes["flavors"]}>
        <ContainerTop bgColor="#b5e8d5" caption="OUR" title="FLAVORS" />
        <Cart />
      </div>
      <Footer />
    </Fragment>
  );
};
