import React, { Fragment } from "react";
import classes from "./Book.module.scss";
import { Footer } from "../../Footer/Footer";
import { ContainerTop } from "../ContainerTop/ContainerTop";
import { ContainerText } from "../About/Containers/ContainerText/ContainerText";
import { ContainerParralax } from "../About/Containers/ContainerParralax/ContainerParralax";
import img from "../../../assets/images/book/1.jpg";

export const Book: React.FC = () => {
  return (
    <Fragment>
      <div className={classes["book"]}>
        <ContainerTop
          bgColor="#ffbfb3"
          caption="CATERING & MORE"
          title="EVENTS"
        />
        <ContainerText
          title="THE BEST ICE CREAM"
          caption="For the best party!"
          paragraph1="I'm a paragraph. Click here to add your own text and edit me. Its easy. Just click Edit Text or double click me to add your own content and make changes to the font. Feel free to drag and drop me anywhere you like on your page. Im a great place for you to tell a story and let your users know a little more about you."
        />
        <div className={classes["book__wrapper"]}>
          <h3 className={classes["book__title"]}>ICE CREAM CART</h3>
          <p className={classes["book__text"]}>4 hr</p>
          <p className={classes["book__text"]}>200$</p>
          <button className={classes["book__btn"]}>BOOK NOW</button>
        </div>
        <ContainerParralax img={img} />
      </div>
      <Footer />
    </Fragment>
  );
};
