import img1 from "../../../assets/images/home/3.jpg";
import img2 from "../../../assets/images/home/4.jpg";

const initialState = {
  img: [img1, img2],
};
export const reducerHome = (state = initialState, action) => {
  return state;
};
export const mapStateToProps = (store) => ({ img: store.reducerHome.img });
