import React from "react";
import classes from "./Social.module.scss";
import { connect } from "react-redux";
import {
  mapStateToProps,
  mapDispatchToProps,
} from "../../../redux/reducers/reducerNav/reducerNav";

const SocialRender: React.FC<{
  isMenuBtnActive: boolean;
  setIsMenuBtnActive: Function;
}> = ({ isMenuBtnActive, setIsMenuBtnActive }) => {
  return (
    <div
      className={
        isMenuBtnActive
          ? `${classes["social"]} ${classes["social_active"]}`
          : classes["social"]
      }
    >
      <a
        href="https://www.facebook.com/"
        className={classes["social__link"]}
        title="facebook"
        target="_blank"
        rel="noreferrer"
        onClick={() => setIsMenuBtnActive()}
      >
        <img
          className={classes["social__img"]}
          src="https://img.icons8.com/ios-filled/50/000000/facebook-new.png"
          alt="social-link"
        />
      </a>
      <a
        href="https://www.twitter.com/"
        className={classes["social__link"]}
        title="twitter"
        target="_blank"
        rel="noreferrer"
        onClick={() => setIsMenuBtnActive()}
      >
        <img
          className={classes["social__img"]}
          src="https://img.icons8.com/ios-filled/50/000000/twitter-circled--v1.png"
          alt="social-link"
        />
      </a>
      <a
        href="https://www.instagram.com/"
        className={classes["social__link"]}
        title="instagram"
        target="_blank"
        rel="noreferrer"
        onClick={() => setIsMenuBtnActive()}
      >
        <img
          className={classes["social__img"]}
          src="https://img.icons8.com/ios-glyphs/30/000000/instagram-circle.png"
          alt="social-link"
        />
      </a>
    </div>
  );
};
export const Social = connect(
  mapStateToProps,
  mapDispatchToProps
)(SocialRender);
