import React, { useState } from "react";
import classes from "./AsideLeft.module.scss";
import { NavLink } from "react-router-dom";
import { Accordion, AccordionSummary } from "@material-ui/core";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";

export const AsideLeft = () => {
  const [accordionExpanded, setAccordionExpanded] = useState(false);
  return (
    <div className={classes["aside-left"]}>
      <Accordion
        expanded={accordionExpanded}
        onClick={() => setAccordionExpanded(!accordionExpanded)}
      >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
          className={classes["aside-left__accordion-summary"]}
        >
          ICE CREAM MENU
        </AccordionSummary>
        <div className={classes["aside-left__accordion-content"]}>
          <NavLink
            to="/flavors/icecream"
            className={classes["aside-left__btn-option"]}
            activeClassName={classes["aside-left__btn-option_selected"]}
            onClick={() => setAccordionExpanded(!accordionExpanded)}
          >
            Ice Cream Flavors
          </NavLink>
          <NavLink
            to="/flavors/dairyfree"
            className={classes["aside-left__btn-option"]}
            activeClassName={classes["aside-left__btn-option_selected"]}
            onClick={() => setAccordionExpanded(!accordionExpanded)}
          >
            Dairy Free Flavors
          </NavLink>
        </div>
      </Accordion>
    </div>
  );
};
