import React, { Fragment } from "react";
import classes from "./About.module.scss";
import { Footer } from "../../Footer/Footer";
import { ContainerTop } from "../ContainerTop/ContainerTop";
import { ContainerText } from "./Containers/ContainerText/ContainerText";
import { ContainerParralax } from "./Containers/ContainerParralax/ContainerParralax";
import img from "../../../assets/images/about/1.jpg";

export const About: React.FC = () => {
  return (
    <Fragment>
      <div className={classes["about"]}>
        <ContainerTop bgColor="#c3a9d4" caption="ABOUT" title="GELATO" />
        <ContainerText
          title="OUR PLACE"
          caption="We just love ice cream!"
          paragraph1="I'm a paragraph. Click here to add your own text and edit me. Its easy. Just click Edit Text or double click me to add your own content and make changes to the font. Feel free to drag and drop me anywhere you like on your page. Im a great place for you to tell a story and let your users know a little more about you."
          paragraph2="This is a great space to write long text about your company and your services. You can use this space to go into a little more detail about your company. Talk about your team and what services you provide. Tell your visitors the story of how you came up with the idea for your business and what makes you different from your competitors. Make your company stand out and show your visitors who you are."
        />
        <ContainerParralax img={img} />
        <ContainerText
          title="OUR LITTLE FACTORY"
          caption="It's what's on the inside that counts"
          paragraph1="I'm a paragraph. Click here to add your own text and edit me. Its easy. Just click Edit Text or double click me to add your own content and make changes to the font. Feel free to drag and drop me anywhere you like on your page. Im a great place for you to tell a story and let your users know a little more about you."
          paragraph2="This is a great space to write long text about your company and your services. You can use this space to go into a little more detail about your company. Talk about your team and what services you provide. Tell your visitors the story of how you came up with the idea for your business and what makes you different from your competitors. Make your company stand out and show your visitors who you are."
        />
      </div>
      <Footer />
    </Fragment>
  );
};
