import React from "react";
import classes from "./FooterSocial.module.scss";

export const FooterSocial: React.FC = () => {
  return (
    <div className={classes["footer-social"]}>
      <a
        href="https://www.facebook.com/"
        className={classes["footer-social__link"]}
        title="facebook"
        target="_blank"
        rel="noreferrer"
      >
        <img
          className={classes["footer-social__img"]}
          src="https://img.icons8.com/ios-filled/50/ffffff/facebook-new.png"
          alt="footer-social-link"
        />
      </a>
      <a
        href="https://www.twitter.com/"
        className={classes["footer-social__link"]}
        title="twitter"
        target="_blank"
        rel="noreferrer"
      >
        <img
          className={classes["footer-social__img"]}
          src="https://img.icons8.com/ios-filled/50/ffffff/twitter-circled--v1.png"
          alt="footer-social-link"
        />
      </a>
      <a
        href="https://www.instagram.com/"
        className={classes["footer-social__link"]}
        title="instagram"
        target="_blank"
        rel="noreferrer"
      >
        <img
          className={classes["footer-social__img"]}
          src="https://img.icons8.com/ios-glyphs/30/ffffff/instagram-circle.png"
          alt="footer-social-link"
        />
      </a>
    </div>
  );
};
