import React from "react";
import classes from "./Container1.module.scss";
import { HomeLink } from "../../../HomeLink/HomeLink";
import { NavLink } from "react-router-dom";

export const Container1: React.FC = () => {
  return (
    <div className={classes["container-1"]}>
      <HomeLink />
      <h2 className={classes["container-1__title-secondary"]}>HAND CRAFTED</h2>
      <h1 className={classes["container-1__title-main"]}>ICE CREAM</h1>
      <NavLink to="/flavors" className={classes["container-1__btn"]}>
        MENU
      </NavLink>
    </div>
  );
};
