import React from "react";
import classes from "./NavMenuBtn.module.scss";
import { connect } from "react-redux";
import {
  mapStateToProps,
  mapDispatchToProps,
} from "../../../redux/reducers/reducerNav/reducerNav";

const NavMenuBtnRender = ({ isMenuBtnActive, setIsMenuBtnActive }) => {
  return (
    <div className={classes["nav-menu"]}>
      <button
        onClick={() => setIsMenuBtnActive()}
        className={
          isMenuBtnActive
            ? `${classes["nav-menu__btn"]} ${classes["nav-menu__btn_active"]}`
            : classes["nav-menu__btn"]
        }
      ></button>
    </div>
  );
};

export const NavMenuBtn = connect(
  mapStateToProps,
  mapDispatchToProps
)(NavMenuBtnRender);
