import React from "react";
import classes from "./HomeLink.module.scss";
import { NavLink } from "react-router-dom";

export const HomeLink: React.FC = () => {
  return (
    <NavLink
      to="/home"
      className={classes["home-link"]}
      activeClassName={classes["home-link_selected"]}
    >
      Gelato
    </NavLink>
  );
};
