import React from "react";
import classes from "./ContainerParralax.module.scss";

export const ContainerParralax: React.FC<{ img: string }> = ({ img }) => {
  return (
    <div
      className={classes["container-parralax"]}
      style={{
        backgroundPosition: `50% 100%`,
        backgroundImage: `url(${img})`,
      }}
    ></div>
  );
};
