const SET_IS_MENU_BTN_ACTIVE = "SET_IS_MENU_BTN_ACTIVE";

const initialState = {
  isMenuBtnActive: false,
};

export const reducerNav = (state = initialState, action) => {
  switch (action.type) {
    case SET_IS_MENU_BTN_ACTIVE:
      return { isMenuBtnActive: !state.isMenuBtnActive };
    default:
      return state;
  }
};

export const mapStateToProps = (state) => ({
  isMenuBtnActive: state.reducerNav.isMenuBtnActive,
});

export const mapDispatchToProps = (dispatch) => ({
  setIsMenuBtnActive: () => dispatch({ type: "SET_IS_MENU_BTN_ACTIVE" }),
});
