import React, { useState, useEffect } from "react";
import {
  CarouselProvider,
  Slider,
  Slide,
  ButtonBack,
  ButtonNext,
} from "pure-react-carousel";
import "pure-react-carousel/dist/react-carousel.es.css";
import classes from "./Carousel.module.scss";
import { ModalImgCarousel } from "../../../../Modals/ModalImgCarousel/ModalImgCarousel";

function importAll(r: any) {
  let imgFolder = {} as any;
  r.keys().map((item: any, index: any) => {
    imgFolder[item.replace("./", "")] = r(item);
    return null;
  });
  return imgFolder;
}
const imgFolder = importAll(
  require.context(
    "../../../../../assets/images/carousel",
    false,
    /\.(png|jpe?g|svg)$/
  )
);

export const Carousel: React.FC = () => {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedImg, setSelectedImg] = useState<string>();
  const [visibleSlides, setVisibleSlides] = useState(4);
  const [images, setImages] = useState<string[]>([]);
  const importImg = () => {
    for (let key in imgFolder) {
      setImages((images) => [...images, imgFolder[key].default]);
    }
  };
  useEffect(() => {
    if (window.matchMedia("(max-width: 574px)").matches) {
      setVisibleSlides(1);
    }
  }, []);
  useEffect(() => {
    importImg();
  }, []);

  return (
    <CarouselProvider
      naturalSlideWidth={100}
      naturalSlideHeight={100}
      totalSlides={images.length}
      visibleSlides={visibleSlides}
      step={visibleSlides}
      className={classes["carousel"]}
    >
      {/* {console.log(images)} */}
      <Slider className={classes["carousel__slider"]}>
        {images.map((el, idx) => {
          const newIdx: number = +el.slice(14, 16);
          return (
            <Slide
              index={newIdx}
              className={classes["carousel__slide"]}
              key={newIdx}
            >
              <div className={classes["carousel__slide-wrapper"]}>
                <img src={el} alt="img" className={classes["carousel__img"]} />
                <div
                  className={classes["carousel__img-hover"]}
                  onClick={() => {
                    setIsModalOpen(true);
                    setSelectedImg(el);
                  }}
                ></div>
              </div>
            </Slide>
          );
        })}
      </Slider>
      <ButtonBack className={classes["carousel__btn"]}>&lsaquo;</ButtonBack>
      <ButtonNext
        className={`${classes["carousel__btn"]} ${classes["carousel__next-button"]}`}
      >
        &rsaquo;
      </ButtonNext>
      <ModalImgCarousel
        isOpen={isModalOpen}
        img={selectedImg}
        onClose={() => {
          setIsModalOpen(!isModalOpen);
        }}
      />
    </CarouselProvider>
  );
};
