import React, { useState, useEffect } from "react";
import classes from "./Cart.module.scss";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { AsideLeft } from "./components/AsideLeft/AsideLeft";
import { AsideRight } from "./components/AsideRight/AsideRight";
import { Menu } from "./components/Menu/Menu";

export const Cart = () => {
  const [iceCream, setIceCream] = useState([]);
  const [dairyFree, setDairyFree] = useState([]);
  const [cart, setCart] = useState([]);
  const [subtotal, setSubtotal] = useState(0);

  useEffect(() => {
    let id = 0;
    const importIceCream = (jsonArr, setInto) => {
      for (let key in jsonArr) {
        const item = {
          src: "",
          title: "",
          description: "",
          price: 0,
          id: 0,
          quantity: 0,
        };
        item.src = jsonArr[key].default;
        item.title = jsonArr[key].default.slice(14).slice(0, -13);
        item.description =
          "This is an item on your menu. Give your item a brief description";
        item.price = 9;
        item.id = id++;
        setInto((prev) => [...prev, item]);
      }
    };
    importIceCream(ICE_CREAM, setIceCream);
    importIceCream(DAIRY_FREE, setDairyFree);
  }, []);

  const addToCart = (item) => {
    if (!cart.includes(item)) {
      item.quantity++;
      setCart((cart) => [...cart, item]);
    } else {
      addItem(item);
    }
    setSubtotal(subtotal + item.price);
  };
  const addItem = (item) => {
    setCart(
      cart.map((el) => {
        if (el.title === item.title) {
          el.quantity++;
          return el;
        }
        return el;
      })
    );
  };
  const removeItem = (item) => {
    let allowFilter = false;
    setCart(
      cart.map((el) => {
        if (el.title === item.title) {
          el.quantity--;
          if (!el.quantity) {
            allowFilter = true;
          }
          return el;
        }
        return el;
      })
    );
    if (allowFilter) {
      setCart(cart.filter((el) => el.quantity > 0));
    }
    setSubtotal(subtotal - item.price);
  };

  return (
    <div className={classes["cart"]}>
      <Router>
        <AsideLeft />
        <div className={classes["cart__menu"]}>
          <Switch>
            <Route exact path="/flavors/icecream">
              <Menu
                title="ICE CREAM FLAVORS"
                data={iceCream}
                onChoose={(el) => addToCart(el)}
              />
            </Route>
            <Route path="/flavors/dairyfree">
              <Menu
                title="DAIRY FREE FLAVORS"
                data={dairyFree}
                onChoose={(el) => addToCart(el)}
              />
            </Route>
          </Switch>
        </div>
        <AsideRight
          cart={cart}
          subtotal={subtotal}
          onAddItem={(el) => addItem(el)}
          onRemoveItem={(el) => removeItem(el)}
        />
      </Router>
    </div>
  );
};

function importAll(r) {
  let images = {};
  r.keys().map((item, index) => {
    images[item.replace("./", "")] = r(item);
    return null;
  });
  return images;
}

const ICE_CREAM = importAll(
  require.context("../../assets/images/iceCream", false, /\.(png|jpe?g|svg)$/)
);
const DAIRY_FREE = importAll(
  require.context(
    "../../assets/images/iceCream/dairyFree",
    false,
    /\.(png|jpe?g|svg)$/
  )
);
