import React from "react";
import classes from "./ContainerTop.module.scss";
import { HomeLink } from "../HomeLink/HomeLink";

export const ContainerTop: React.FC<{
  bgColor: string;
  caption: string;
  title: string;
}> = ({ bgColor, caption, title }) => {
  return (
    <div
      className={classes["container-top"]}
      style={{ backgroundColor: `${bgColor}` }}
    >
      <HomeLink />
      <h3 className={classes["container-top__caption"]}>{caption}</h3>
      <h2 className={classes["container-top__title"]}>{title}</h2>
    </div>
  );
};
