import React from "react";
import classes from "./Nav.module.scss";
import { NavLink } from "react-router-dom";
import { connect } from "react-redux";
import {
  mapStateToProps,
  mapDispatchToProps,
} from "../../../redux/reducers/reducerNav/reducerNav";

const NavRender: React.FC<{
  isMenuBtnActive: boolean;
  setIsMenuBtnActive: Function;
}> = ({ isMenuBtnActive, setIsMenuBtnActive }) => {
  return (
    <nav
      className={
        isMenuBtnActive
          ? `${classes["nav"]} ${classes["nav_active"]}`
          : classes["nav"]
      }
    >
      <ul className={classes["nav__list"]}>
        <li className={classes["nav__list-item"]}>
          <NavLink
            to="/home"
            className={classes["nav__link"]}
            activeClassName={classes["nav__link_selected"]}
            onClick={() => setIsMenuBtnActive()}
          >
            HOME
          </NavLink>
        </li>
        <li className={classes["nav__list-item"]}>
          <NavLink
            to="/flavors/icecream"
            className={classes["nav__link"]}
            activeClassName={classes["nav__link_selected"]}
            onClick={() => setIsMenuBtnActive()}
          >
            FLAVORS
          </NavLink>
        </li>
        <li className={classes["nav__list-item"]}>
          <NavLink
            to="/book"
            className={classes["nav__link"]}
            activeClassName={classes["nav__link_selected"]}
            onClick={() => setIsMenuBtnActive()}
          >
            BOOK AN EVENT
          </NavLink>
        </li>
        <li className={classes["nav__list-item"]}>
          <NavLink
            to="/about"
            className={classes["nav__link"]}
            activeClassName={classes["nav__link_selected"]}
            onClick={() => setIsMenuBtnActive()}
          >
            ABOUT
          </NavLink>
        </li>
      </ul>
    </nav>
  );
};
export const Nav = connect(mapStateToProps, mapDispatchToProps)(NavRender);
