import React from "react";
import classes from "./Menu.module.scss";

export const Menu = ({ title, data, onChoose }) => {
  return (
    <div className={classes["menu"]}>
      <h1 className={classes["menu__title"]}>{title}</h1>
      <hr className={classes["menu__divider"]} />
      <div className={classes["menu__grid"]}>
        {data.map((el, idx) => {
          return (
            <div
              className={classes["menu__grid-item"]}
              key={el.id}
              onClick={() => onChoose(el)}
            >
              <img
                src={el.src}
                alt={el.title}
                className={classes["menu__grid-item-img"]}
              />
              <h2 className={classes["menu__grid-item-title"]}>{el.title}</h2>
              <hr className={classes["menu__divider"]} />
              <p className={classes["menu__grid-item-text"]}>
                {el.description}
              </p>
              <p className={classes["menu__grid-item-text"]}>{el.price}$</p>
            </div>
          );
        })}
      </div>
    </div>
  );
};
